<template>
  <div class="index">
    <mu-row>
      <mu-col span="3"><div class="grid-cell">
        <list></list>
      </div></mu-col>
      <mu-col span="9"><div class="grid-cell">
        <RightSide></RightSide>
      </div></mu-col>
    </mu-row>
  </div>
</template>

<script>

  import list from "./list";
  import RightSide from "./RightSide";
    export default {
      name: "index",
      components:{
        list,
        RightSide,
      },
    }
</script>

<style scoped>

  .grid-cell {
    border-radius: 0px;
    height: 100%;
    background: #b3d8ff;
  }
</style>



