<template>
    <div class="RightSide">
        <mu-tabs :value.sync="active">
          <mu-tab>首页</mu-tab>
          <mu-tab>地图浏览</mu-tab>
          <mu-tab>地图</mu-tab>
        </mu-tabs>
        <div class="right" v-if="active === 0">
          <RightSideIndex></RightSideIndex>
        </div>
        <div class="demo-text" v-if="active === 1">
          <p>“我的心从来没有这么坚定过，所以我会为了补偿而死，也可以为了补偿而死……一辈子，急辈子都无所谓，我绝不后退！”</p>
          <p>“如果我后退呢？如果我想要死呢？我不想你再次背对着我逃跑了……”</p>
          <p>“那么就去地狱找到你，我绝对不逃！”</p>
          <p>“白痴，你也哭了？因为那些软弱拖累你的脚步？”</p>
        </div>
        <div class="demo-text" v-if="active === 2">
          <p>“不，这泪水……是因为勇气，因为力量，因为信任，……你不会懂的！”</p>
          <p>“我不会帮你，想要什么样的未来……自己去追寻吧！”</p>
          <p>“我不需要你的帮忙！未来我会一手开启，什么样的敌人我也不会惧怕……还有，其实我们可以成为朋友的……”</p>
          <p>“也许吧，未来……给你了。”</p>
        </div>
    </div>
</template>

<script>

  import RightSideIndex from "./RightSideIndex";
  import RightSideMapScan from "./RightSideMapScan";
  import RightSideMap from "./RightSideMap";
    export default {
        name: "RightSide",
        components:{
          RightSideMap,
          RightSideMapScan,
          RightSideIndex
        },
        data(){
          return{
            active:0
          };
        },
    }
</script>

<style scoped>
  .right{
    width: 100%;
    height: 835px;
  }
</style>



